body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'montserrat';
}
.Side-element-item {
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    text-align: left;
    font-size: 14px;
}

.Header {
    padding-top: 50px;
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 50px;
    background-color: #187ab3;
    min-height: 35vh;
    display: flex;
    flex-direction: row;
    justify-content: left;
    font-size: calc(5px + 2vmin);
    color: white;
}

.Side-element {
    padding-top: 30px;
    box-sizing: border-box;
    text-align: left;
    padding-left: 500px;
    flex-direction: column;
    align-items: flex-start;
}

.My-name{
    flex-direction: column;
    font-size: 50;
    text-align: left;
    align-items: flex-start;
}

.Side-element-heading {
    font-size: 16px;
    font-weight: bold;
}

.Header-logo {
    font-weight:bolder;
    font-size: 10;
    flex-direction: column;
    justify-content: left;
    text-align: left;
    padding-left: 50px;
}
.Body {
    padding-left: 150px;
    padding-right: 100px;
    max-width: 90%;
    flex-direction: column;
    justify-content: left;
    align-items: left;
    justify-content: left;
}

.Body-item-heading {
    font-weight: bold;
    font-size: 20px;
}
.Body-item {
    text-align: left;
    padding-bottom: 10px;

}

.Title {
    font-weight: bold;  
}

.Line-text {
    flex-direction: row;
}
.App-footer {
    padding-top: 20px;
    background-color: #282c34;
    min-height: 10vh;
    color: aliceblue;
    font-size: 24px;
  }
